import React from "react";
import { useState } from "react";
import { handleAddItem } from "util/utils";
import { InputImageFile } from "components/InputImageFile";
import { InputDate } from "components/InputDate";
import { MapContainer } from "./MapContainer";

// import { mock as userStore } from "./mock";
const SubmitContainer = () => {
  // const [date, setDate] = useState(new Date());
  const [form, setForm] = useState({
    name: "", // 
    password: "", //
    object: "", // 
    place: "", //
    locker: "", // 
    memo: "", //
    imagePath: "",
    date: new Date(),
    location: {
      lat: "",
      lng: "",
    },
  });

  const onChange = (e) => {
    console.log(form);
    const { value, name } = e.target;
    setForm((state) => ({
      ...state,
      [name]: value,
    }));
  };
  const showData = () => {
    console.log(form);
  };

  const onSubmit = (e) => {
    handleAddItem(form);
    console.log("");
    // firebase   ()
    //  ()
    //
    setForm({
      name: "", // 
      password: "",
      object: "", // 
      place: "", //
      locker: "", // 
      memo: "", //
      imagePath: "",
      location: {
        lat: "",
        lng: "",
      },
      date: new Date(),
    });
    e.preventDefault();
  };
  return (
    <div className=" max-w-md mx-auto max-h-full sm:max-w-3xl">
      <div className="bg-white px-4 py-1">
        <form onSubmit={onSubmit} className="w-full sm:max-w-3xl sm:text-sm">
          <div className="flex flex-wrap -mx-3  md:mb-6">
            <div className="w-1/2 md:w-1/4  px-3 md:mb-0">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                
              </label>
              <input
                className="appearance-none block w-full bg-gray-200 text-gray-700 border  rounded py-3 px-4  leading-tight focus:outline-none focus:bg-white"
                id="input-name"
                type="text"
                placeholder=" "
                onChange={onChange}
                name="name"
                value={form.name}
                maxlength="10"
              />
            </div>
            <div className="w-1/2 md:w-1/4 px-3 md:mb-0">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                
              </label>
              <input
                className="appearance-none block w-full bg-gray-200 text-gray-700 border  rounded py-3 px-4  leading-tight focus:outline-none focus:bg-white"
                id="input-password"
                type="password"
                placeholder=""
                onChange={onChange}
                name="password"
                value={form.password}
                autoComplete="off"
                maxlength="15"
              />
            </div>
            <div className="w-1/2 md:w-1/4 px-3">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                
              </label>
              <input
                className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                id="grid-city"
                type="text"
                placeholder=""
                onChange={onChange}
                name="object"
                value={form.object}
                maxlength="15"
              />
            </div>
            <div className="w-1/2 md:w-1/4 px-3 md:mb-6">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                
              </label>
              <div>
                <InputDate
                  userDoc={form}
                  onChange={onChange}
                  data={form.date}
                  setData={setForm}
                />
              </div>
            </div>
          </div>
          <div className="flex flex-wrap -mx-3 mb-6">
            <div className="w-full md:w-1/3 px-3 md:mb-6">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                 
              </label>
              <input
                className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                id="grid-last-name"
                type="text"
                placeholder=" "
                onChange={onChange}
                name="place"
                value={form.place}
                maxlength="15"
              />
            </div>
            <div className="w-full md:w-1/3 px-3 md:mb-6">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                
              </label>
              <input
                className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                type="text"
                placeholder=" "
                onChange={onChange}
                name="locker"
                value={form.locker}
                maxlength="20"
              />
            </div>
            <div className="w-full md:w-1/3 px-3 md:mb-6">
              <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                
              </label>
              <input
                className="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                id="grid-zip"
                type="text"
                placeholder=""
                onChange={onChange}
                name="memo"
                value={form.memo}
                maxlength="100"
              />
            </div>
          </div>
          <div className="flex flex-wrap -mx-3 mb-2">
            <div className="w-full md:w-1/2 px-3 mb-15 md:mb-0">
              <InputImageFile data={form} setData={setForm} />
            </div>
            <div className="w-full md:w-1/2 px-3 mb-15 md:mb-0">
              <MapContainer setData={setForm} />
            </div>
          </div>

          <div className="mt-10 sm:mt-20">
            <button
              type="submit"
              onClick={showData}
              className="block w-full  px-4 py-2 border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-800 focus:outline-none focus:ring-2 focus-offset-2 focus:ring-indigo-500 "
            >
              Submit
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default SubmitContainer;
