import { getRegExp } from "korean-regexp";

function (targetText, input) {
  let  = getRegExp(input, {
    initialSearch: true,
    fuzzy: true,
  });
  if (targetText.search() > -1) return true;
}
export default function (arr, input, filterCondition) {
  return arr.filter((val) => (val[`${filterCondition}`], input));
}
